}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Montserrat", sans-serif;
}

h2 {
  margin: 1.5rem 0;
  font-size: 2rem;
  width: 100%;
  text-align: center;
}

h3,
h4 {
  font-family: "Montserrat", sans-serif;
}

::-webkit-scrollbar {
  background-color: #1b262c;
  width: 5px;
