.Hamburger {
  cursor: pointer;
  color: #c4e7ff;
  z-index: 300;
  transition: all 0.3s ease-out;
}

.Hamburger:hover {
  color: #0f4c75;
}

.Hamburger:focus {
  color: #c4e7ff;
}

.Hamburger svg {
  font-size: 40px;
}

@media (min-width: 769px) {
  .Hamburger {
    display: none;
  }
}

@media (max-width: 375px) and (orientation: portrait) {
  .Hamburger svg {
    font-size: 30px;
  }
}

@media (max-height: 375px) and (orientation: landscape) {
  .Hamburger svg {
    font-size: 30px;
  }
}
