.Sidebar img {
  border-radius: 50%;
  width: 50%;
  margin: 0 25%;
}

.desktopMini {
  position: fixed;
  width: 100px;
  height: 100vh;
  background-color: #1b262c;
  background-image: linear-gradient(120deg, #2e3346, #0c0c0c);
  display: flex;
  flex-flow: column;
  z-index: 200;
  padding-top: 30px;
  align-items: center;
  overflow-y: auto;
}

.profile2 {
  border-radius: 50%;
  width: 60px;
  margin: 5px;
  padding: 5px;
}

.expandedNav {
  width: 150px;
  position: fixed;
  height: 100vh;
  margin-left: 100px;
  background-color: #1b262c;
  background-image: linear-gradient(50deg, #0c0c0c, #2e3346);
  display: flex;
  flex-flow: column;
  z-index: 150;
  padding: 30px 0 20px 0;
  align-items: center;
  overflow-y: auto;
}

.desktopMini > *,
.desktopMini > ul > li,
.desktopMini > ul > li {
  padding: 15px 0;
}

.expandedNavLinks {
  padding: 15px 0;
  height: 4rem;
  text-decoration: none;
  color: #c4e7ff;
  font-family: "Montserrat", sans-serif;
  transition: 0.2s;
  transition-timing-function: ease;
  display: flex;
  align-items: center;
}

.expandedNav > *,
.expandedNav > ul > li,
.expandedNav > ul > li {
  padding: 15px 0;
}

.expandedNavLinks:hover {
  color: #0f4c75;
}

.enter {
  transform: translateX(-100%);
}
.enterActive {
  transform: translateX(0);
  transition: transform 300ms;
}
.exit {
  transform: translateX(0);
}
.exitActive {
  transform: translateX(-100%);
  transition: transform 300ms;
}

@media screen and (hover: none) {
  .expandedNav {
    display: none;
  }
}
