.navHamburger {
  width: 100%;
  height: 60px;
  position: fixed;
  top: 0;
  bottom: 0;
  z-index: 300;
  padding: 10px;
  background-color: #1b262cd4;
}

@media (max-width: 375px) {
  .navHamburger {
    padding: 5px 10px;
    height: fit-content;
  }
}

